<template>
  <!-- 产品列表 -->
  <div class="president">
    <van-search
      v-model="value"
      shape="round"
      background="#e93323"
      placeholder="搜索商品信息"
      @search="onSearch"
    />
    <van-grid>
      <van-grid-item class="red" :text="title" />
      <van-grid-item
        :class="{ red: condition.priceOrder != '' }"
        @click="orderPrice"
        :text="pricetext"
      />
      <van-grid-item
        :class="{ red: condition.salesOrder != '' }"
        @click="orderSales"
        :text="salestext"
      />
      <van-grid-item class="red" text="新品" />
    </van-grid>
    <div class="item">
      <van-grid :column-num="3">
        <van-grid-item
          v-for="(item, index) in list"
          :key="index"
          text="文字"
          :to="`/detail/${item.id}`"
        >
          <van-image class="Premiumesimg" :src="item.image" />
          <p class="p1">{{ item.store_name }}</p>
          <p class="p2">￥{{ item.price }}</p>
          <p class="p3">
            <span
              >￥{{ item.vip_price }}
              <img src="../assets/vip.png" alt="" /></span
            ><span>已售{{ item.sales }}</span>
          </p>
        </van-grid-item>
      </van-grid>
    </div>
    <div class="pictrue" v-show="list&&list.length == 0">
      <img src="../assets/shangping.png" alt="" />
    </div>
  </div>
</template>
<script>
import { mapMutations } from "vuex";
export default {
  props: ["id"],
  data() {
    return {
      condition: {
        page: 1, //页码
        limit: 8, //没有显示的条数
        keyword: "", //关键字
        sid: 31, //分类
        news: 0,
        priceOrder: "", //asc,desc
        salesOrder: "", //asc,desc
      },
      list: [],
      value: "",
      pricetext: "价格",
      salestext: "销量",
      title: "全部",
    };
  },
  created() {
    this.changeLoading(true);
    this.condition.sid = this.id;
    this.title = this.$route.query.title;
    this.searchs();
  },
  methods: {
    onSearch(value) {
      let newarr = [];
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].store_name.indexOf(value) != -1) {
          newarr.push(this.list[i]);
        }
      }
      this.list = newarr;
    },
    //销量排序
    orderSales() {
      this.changeLoading(true);
      this.condition.priceOrder = "";
      if (this.condition.salesOrder == "") {
        this.condition.salesOrder = "desc";
        this.salestext = "销量降序";
      } else if (this.condition.salesOrder == "desc") {
        this.condition.salesOrder = "asc";
        this.salestext = "销量升序";
      } else {
        this.condition.salesOrder = "";
        this.salestext = "销量";
      }
      this.searchs();
    },
    //价格排序
    orderPrice() {
      this.changeLoading(true);
      this.condition.salesOrder = "";
      if (this.condition.priceOrder == "") {
        this.condition.priceOrder = "desc";
        this.pricetext = "价格降序";
      } else if (this.condition.priceOrder == "desc") {
        this.condition.priceOrder = "asc";
        this.pricetext = "价格升序";
      } else {
        this.condition.priceOrder = "";
        this.pricetext = "价格";
      }
      this.searchs();
    },
    searchs() {
      this.axios.get("/api/products", { params: this.condition }).then((d) => {
        this.list = d.data.data;
        console.log(this.list);
        this.changeLoading(false);
      });
    },
    ...mapMutations(["changeLoading"]),
  },
};
</script>
<style lang="less">
.president {
  .red {
    span {
      &.van-grid-item__text {
        color: red;
      }
    }
  }
  .item {
    .Premiumesimg {
      width: 160.5px;
      height: 160.5px;
    }
    p {
      margin: 0;
    }
    .p1 {
      width: 160.5px;
      font-size: 15px;
      margin-top: 6px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .p2 {
      width: 160.5px;
      font-size: 17px;
      font-weight: 700;
      color: #fc4141;
    }
    .p3 {
      width: 160.5px;
      font-size: 12px;
      font-weight: 700;
      margin: 0 0 0 5px;
      display: flex;
      justify-content: space-between;
      span {
        img {
          width: 22.7px;
          height: 10.5px;
        }
        &:nth-child(2) {
          font-weight: 200;
          color: #aaa;
        }
      }
    }
  }
  .pictrue{
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        margin: 35px 0 25px 0;
        width: 200px;
      }
    }
}
</style>